import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';

import variables from '../styles/variables';

const MetadataWrapper = styled.div`
  display: inline-block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 15rem;
  font-size: 24px;

  @media only screen and (max-width: ${variables.screenWidth}) {
    font-size: 12px;
    left: 12rem;
  }
`;

const NowPlaying = styled.div`
  color: ${props => props.theme.colorNowPlaying};
  margin-top: 4.5rem;

  @media only screen and (max-width: ${variables.screenWidth}) {
    margin-top: 3.5rem;
  }
`;

const Title = styled.div`
  color: ${props => props.theme.colorSongTitle};

  @media only screen and (max-width: ${variables.screenWidth}) {
    max-width: 25rem;
  }
`;

function useInterval(callback, delay) {
  const savedCallback = useRef();

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}

const Metadata = () => {
  const [stats, setStats] = useState(null);

  useInterval(() => {
    fetch('https://stream2.mondkapjefm.nl:8443/status-json.xsl')
    .then(r => r.json())
    .then(r => {setStats(r)});
  }, 1000);

  return(
    <MetadataWrapper>
      <NowPlaying>Now playing:</NowPlaying>
      {stats != null && <Title>{stats.icestats.source.title}</Title>}
    </MetadataWrapper>
  );
};

export default Metadata;
